.container_work{
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  gap: 3em;
  color: $color2;
  @include responsive(m){
    gap: 6em;
  }

    // Title
    .title{
      margin-top: 7em;
      display:flex;
      width:85%;
      @include responsive(m){
        width: 80%;
      }
      @include responsive(hd){
        width: 75%;
        margin-top: 10em;
      }
  
      &_number{
        font-size:34px;
        position: absolute;
        margin-left: 10px;
        @include responsive(m){
        font-size: 55px;
        }
        @include responsive(hd){
        font-size: 72px;
        }
      }
  
      &_name{
        display: flex;
        flex-direction: row;
        font-size:70px;
        width:100%;
        left:-65px;
        @include responsive(m){
        font-size: 110px;
        }
        @include responsive(hd){
        font-size: 144px;
        }
        
        &:before{
        content: "";
        width:50px;
        border-bottom: 2px solid $color2;
        margin: auto 10px auto 0;
        @include responsive(m){
        width:75px;
        }
        @include responsive(hd){
        width:100px;
        }
        }
        &:after{
        content: "";
        flex: 1 1;
        border-bottom: 2px solid $color2;
        margin: auto 0 auto 10px;
        }
      }
    }


      // Works
      .works{
        width: 75%;
        display: flex;
        flex-direction: column;
        gap: 1em;
        // background-color: chocolate;
        @include responsive(s){
          width: 65%;
        }
        @include responsive(l){
          width: 55%;
        }
        @include responsive(xl){
          width: 70%;
          flex-direction: row-reverse;
          gap: 5em;
        }
        @include responsive(hd){
          gap: 10em;
          width: 60%;
        }

        // Title Works
        h5{
          font-size: 15px;
          @include responsive(m){
            font-size: 20px;
          }
          @include responsive(xl){
            display: none;
          }
        }

        // Image Works
        img{
          width: 100%;
          @include responsive(xl){
            min-width: 50%;
          }
          @include responsive(hd){
            min-width: 45%;
          }
        }

        // Works Desc
      &_desc{
          display: flex;
          flex-direction: column;
          justify-content: center;
        h5{
          display: none;
          @include responsive(xl){
            font-size: 20px;
            display: block;
          }
        }
        p{
          margin-top: 1em;
          width: 100%;
          margin-left: 10px;
          font-size: 15px;
          @include responsive(m){
            font-size: 17px;
          }
          @include responsive(l){
            font-size: 20px;
          }
          @include responsive(xl){
            font-size: 23px;
          }
          @include responsive(hd){
            font-size: 25px;
          }
        }

        .works_link{
          margin-top: 1em;
          margin-left: 10px;
          display: flex;
          gap: 14px;
          span
          svg{
            width: 20px;
            height: 20px;
            @include responsive(m){
              width: 25px;
              height: 25px;
            }
            @include responsive(hd){
              width: 28px;
              height: 28px;
            }
          }
          path{
            fill: $color2;
          }
        }
      }
      }

      .works:nth-child(3){
        @include responsive(xl){
          flex-direction: row;
          margin-top: 5em;
        }
        @include responsive(hd){
          margin-top: 8em;
        }
        h5{
          text-align: right;
        }
        .works_desc{
          p{
            text-align: right;
            margin-left: 0;
          }
        }
        .works_link{
          justify-content: end;
        }
      }


      // Cards Works
      .other_works{
        width: 75%;
        display: grid;
        grid-gap: 16px;
        grid-template-columns: auto;
        justify-content: center;
        align-self: center;
        align-items: center;
        @include responsive(s){
          width: 65%;
        }
        @include responsive(m){
          grid-template-columns: repeat(2, auto);
          width: 70%;
        }
        @include responsive(xl){
          grid-template-columns: repeat(3, auto);
        }
        @include responsive(hd){
          width: 60%;
        }

        h5{
          font-size: 15px;
        }

        .card {
          background-color: $color4;
          padding: 1.5em;
          text-align: center;
          display: flex;
          flex-direction: column;
          gap: 1.5em;
          align-items: flex-start;
          h5{
            font-size: 30px;
          }
          p{
            font-size: 15px;
            text-align: left;
            @include responsive(m){
              font-size: 17px;
            }
            @include responsive(l){
              // font-size: 20px;
            }
          }
          .works_link{
            display: flex;
            gap: 14px;
            span svg{
            width: 20px;
            height: 20px;
            @include responsive(m){
              width: 25px;
              height: 25px;
            }
            @include responsive(hd){
              width: 28px;
              height: 28px;
            }
            }
            span path{
              fill: $color2;
            }
          }
        }
        
      }


}